<template>
    <div id="index_banner">
        <div class="main_banner">
            <el-carousel :interval="5000" arrow="always" height="450px">
                <el-carousel-item v-for="item in list" :key="item.id" :style="{'background-image': 'url(' + imgFormatter(item.cover) + ')'}">
                    <router-link :to="getProductUrl(item.url)" v-if="item.url && item.url.indexOf('open:') > -1" target="_blank"></router-link>
                    
                    <router-link :to="handlerUrl(item.url)" v-else-if="item.url && item.url.indexOf('http') == -1"></router-link>
                    
                    <a :href="item.url" v-else-if="item.url && item.url.indexOf('http') > -1" target="_blank"></a>
                    
                </el-carousel-item>
            </el-carousel>

            <div class="announce_list" v-if="announceList.length">
                <div class="container">
                    <vueSeamless :data="announceList" :class-option="{ direction: 2, limitMoveNum: 1 }" class="seamless-warp">
                        <ul class="item">
                            <li v-for="item in announceList" v-text="item.announce_content" :key="item.id"></li>
                        </ul>
                    </vueSeamless>
                </div>
            </div>
        </div>

        <div class="sub_banner">
            <div class="container">
                <el-row :gutter="20">
                    <el-col :span="6" :xs="24">
                        <div class="grid-content bg1">
                            <router-link :target="app.hightOddsId == 11 ? '_self' : '_blank'" :to="app.hightOddsId == 11 ? '/activity/hightodds' : '/p?id=11'" :class="{ hightOdds: app.hightOddsId == 11 }" >
                                <img src="@/assets/sub_banner_1.jpg" />
                                <div>
                                    SG彩票
                                </div>
                            </router-link>
                        </div>
                    </el-col>
                    <el-col :span="6" :xs="24">
                        <div class="grid-content bg2">
                            <router-link target="_blank" to="/p?id=6">
                                <img src="@/assets/sub_banner_2.jpg" />
                                <div>
                                    鑫盛体育
                                </div>
                            </router-link>
                        </div>
                    </el-col>
                    <el-col :span="6" :xs="24">
                        <div class="grid-content bg3">
                            <router-link target="_blank" to="/p?id=1">
                                <img src="@/assets/sub_banner_3.jpg" />
                                <div>
                                    VG棋牌
                                </div>
                            </router-link>
                        </div>
                    </el-col>
                    <el-col :span="6" :xs="24">
                        <div class="grid-content bg4">
                            <router-link to="/product/10">
                                <img src="@/assets/sub_banner_4.jpg" />
                                <div>
                                    女优电子
                                </div>
                            </router-link>
                        </div>
                    </el-col>
                </el-row>
            </div>
        </div>

        <el-dialog
            title="通知"
            :visible.sync="alertDialogShow"
            class="alert_dialog"
        >
            <div style="padding: 0 15px;">
                <!--<p :class="{ h: /[1-9]./.test(item) }" v-for="(item, index) in alertContent" :key="index"> {{ item }} </p>-->
                <div class="alert_item" v-for="(item, index) in alertContent" :key="index">
                    <div class="h" v-if="alertContent.length > 1">公告{{ numberToChinese(index) }}：</div>
                    <p v-for="(item_content, _index) in item" :key="_index"> {{ item_content }} </p>
                </div>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button @click="alertDialogShow = false">知道了</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import request from '@/utils/request'
import { imgFormatter, numberToChinese } from '@/utils/index'
import vueSeamless from 'vue-seamless-scroll'
import { 
    getBannerAndAnnounceList
} from '@/api/common'

export default {
    name: 'Home-Banner',
    props: ['user', 'app', 'product'],

    components: {
        vueSeamless
    },

    data() {
        return {
            announceList: [],
            list: [],
            alertDialogShow: false,
            alertContent: [],
            bannerHeight: 0,
        }
    },
    computed: {
        isDev() {
            return process.env.NODE_ENV == 'development';
        },
        
    },

    mounted() {
        this.init();
    },

    methods: {
       init() {
           getBannerAndAnnounceList().then(json => {
                if (json.banner.code == 200) {
                    let list = json.banner.list.data;
                    
                    this.list = list;
				} else {
                    this.$message.error(json.info);
                }
                
                if(json.announce.code == 200) {
                    const list = json.announce.list.data.filter(item => item.type == 'home')
                    this.announceList = list;

                    const alertList = json.announce.list.data.filter(item => item.type == 'alert');
                    if(alertList.length && !window.alertInit) {
                        window.alertInit = 1;
                        // this.alertContent = alertList[0].announce_content.split(/\n/);
                        this.alertContent = alertList.map(item => item.announce_content.split(/\n/));
                        this.alertDialogShow = true;
                    }
                } else {
                    this.$message.error(json.info);
                }
            })
       },

       imgFormatter(src) {
           return imgFormatter(src)
       },
       
       getProductUrl(url) {
        const id = url.replace('open:', '');
        
        
        let link = '/p?id=';

        if(id == 10 || id == 18) {
            link = '/product/';
        }

        link += id;
        return link;
       },

       handlerUrl(url) {
           let obj;

           try {
              obj = JSON.parse(url);
           }catch(e) {

           }

           if(obj && obj.desktop) {
               return obj.desktop;
           }

           return url;
       },
       
       numberToChinese(index) {
           return numberToChinese(index + 1);
       },
    }
}
</script>

