<template>
    <div id="index">
        <Banner :user="user" :app="app" :product="product" />
    </div>
</template>

<script>
import Banner from './Banner'
import { mapGetters } from 'vuex'

import { imgFormatter } from '@/utils/index'

export default {
    name: 'Home-Index',
    components: {
        Banner,
    },

    data() {
        return {
            dialogVisible: false,
        }
    },

    computed: mapGetters({
        user: 'user',
        app: 'app',
        product: 'product'
    }),
    mounted() {
        const code = this.$route.query.c;
        
        if(code) {
            window.localStorage.setItem('code', code);
            
            this.$router.push({ path: '/' });

            if(this.user.roles.indexOf('user') == -1) {
                this.$nextTick(() => {
                    this.$store.dispatch('openRegisterDialog');
                })
            }
        }
    },

    methods: {
        
    }
}
</script>

